import Inputmask from 'inputmask';
import JustValidate from 'just-validate';
// TODO:          JustValidate
// TODO: "" ""   ,  2   
// TODO: ""        
// TODO:   inputMask
const inputTel = document.querySelector('.booking__tel input');
const telMask = new Inputmask('+7 (999) 999-99-99');
telMask.mask(inputTel);

const justValidate = new JustValidate('.booking__form');
justValidate
    .addField('#name-input', [
      {
        rule: 'required',
        errorMessage: '  ',
      }, {
        rule: 'minLength',
        errorMessage: '  - 2 ',
        value: 2,
      },
      {
        rule: 'customRegexp',
        value: /^[A-Za-z--\s]+$/,
        errorMessage: '    ',
      },
    ])
    .addField('#date-input', [
      {
        rule: 'required',
        errorMessage: ' ',
      },
    ])
    .addField('#time-input', [
      {
        rule: 'required',
        errorMessage: ' ',
      },
    ])
    .addField('#tel-input', [
      {
        rule: 'required',
        errorMessage: '  ',
      },
      {
        validator(value) {
          const phone = inputTel.inputmask.unmaskedvalue();
          return !!(Number(phone) && phone.length === 10); // => boolean
        },
        errorMessage: ' ',
      },
    ])
    .addField('#count-input', [
      {
        rule: 'required',
        errorMessage: ' ',
      },
      {
        rule: 'number',
        errorMessage: ' ',
      },
      {
        rule: 'integer',
        errorMessage: '  ',
      },
    ])
    .addField('#surname-input', [
      {
        rule: 'required',
        errorMessage: '  ',
      },
      {
        rule: 'minLength',
        errorMessage: '  - 2 ',
        value: 2,
      },
      {
        rule: 'customRegexp',
        value: /^[A-Za-z--\s]+$/,
        errorMessage: '    ',
      },
    ])
    .addField('#email-input', [
      {
        rule: 'required',
        errorMessage: '  e-mail',
      },
      {
        rule: 'email',
        errorMessage: 'E-mail ',
      },
    ]);
